.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: center;
}

.main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.sidebar {
  width: 300px;
  background-color: #f5f5f5;
  border-right: 1px solid #ddd;
  padding: 20px;
  overflow-y: auto;
}

.controls {
  margin-bottom: 20px;
}

.node-count-input {
  margin-bottom: 15px;
}

.node-count-input label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.node-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

.node-input:focus {
  outline: none;
  border-color: #007bff;
}

.node-input:disabled {
  background-color: #f8f9fa;
  cursor: not-allowed;
}

.create-btn {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.create-btn:hover:not(:disabled) {
  background-color: #0056b3;
}

.create-btn:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.environments h3 {
  margin-bottom: 15px;
  color: #333;
}

.env-item {
  background: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 10px;
}

.env-item.running {
  border-left: 4px solid #28a745;
}

.env-item.creating {
  border-left: 4px solid #ffc107;
}

.env-item.error {
  border-left: 4px solid #dc3545;
}

.env-info {
  margin-bottom: 10px;
}

.env-name {
  font-weight: bold;
  display: block;
  margin-bottom: 3px;
}

.env-nodes {
  font-size: 12px;
  color: #666;
  display: block;
  margin-bottom: 5px;
}

.env-status {
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 3px;
  text-transform: uppercase;
}

.env-status.running {
  background-color: #d4edda;
  color: #155724;
}

.env-status.creating {
  background-color: #fff3cd;
  color: #856404;
}

.env-status.error {
  background-color: #f8d7da;
  color: #721c24;
}

.env-actions {
  display: flex;
  gap: 10px;
}

.connect-btn, .delete-btn {
  flex: 1;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-size: 12px;
}

.connect-btn {
  background-color: #28a745;
  color: white;
}

.connect-btn:hover:not(:disabled) {
  background-color: #218838;
}

.connect-btn:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.delete-btn {
  background-color: #dc3545;
  color: white;
}

.delete-btn:hover {
  background-color: #c82333;
}

.terminal-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #1e1e1e;
}

.terminal-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.terminal-header {
  background-color: #333;
  color: white;
  padding: 10px 15px;
  font-size: 14px;
  flex-shrink: 0;
}

.terminal {
  flex: 1;
  padding: 10px;
  overflow: hidden;
  position: relative;
}

.terminal-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 20px;
  border-radius: 4px;
  z-index: 10;
}

.no-terminal {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #999;
  font-size: 18px;
}
